enum Api {
  //巡检统计
  getInspectionUrl = '/da/station-operation/devops/inspection',
  //隐患缺陷
  getFaultAndHiddenUrl = '/da/station-operation/devops/fault-hidden',
  //工单统计
  getWorkOrderUrl = '/da/station-operation/devops/work-order',

  //顶端指标
  getAllIndexUrl = '/da/station-operation/devops/top/index',
  //损失电量
  getLossElecUrl = '/da/station-operation/devops/loss/elec',
  //两票
  getTicketcUrl = '/da/station-operation/devops/ticket',

  //安全统计
  getSecurityUrl = '/da/station-operation/devops/safety',
}
export const getInspectionData = (params) =>
  defHttp.post({ url: Api.getInspectionUrl, params }, {});

export const getFaultAndHiddenData = (params) =>
  defHttp.post({ url: Api.getFaultAndHiddenUrl, params }, {});

export const getWorkOrderData = (params) => defHttp.post({ url: Api.getWorkOrderUrl, params }, {});

